import { Scene } from 'phaser';

export class MuseRunnerGame extends Scene {
    private character!: Phaser.GameObjects.Rectangle;
    private platforms!: Phaser.GameObjects.Group;
    private gameKeys!: { [key: string]: Phaser.Input.Keyboard.Key };
    
    // Game state
    private isGameRunning: boolean = false;
    private score: number = 0;
    private streak: number = 0;
    private characterSpeed: number = 120;
    private scoreText!: Phaser.GameObjects.Text;
    private streakText!: Phaser.GameObjects.Text;

    // Platform system
    private currentPlatform: Phaser.GameObjects.Rectangle | null = null;
    private currentKey: string | null = null;
    private platformStartX: number = 0;

    // Musical note frequencies (C major scale)
    private noteFrequencies = {
        'S': 261.63, // C4
        'D': 293.66, // D4
        'F': 329.63, // E4
        'G': 349.23, // F4
        'H': 392.00, // G4
        'J': 440.00, // A4
        'K': 493.88  // B4
    };

    // Audio context for musical notes
    private audioContext: AudioContext | null = null;

    constructor() {
        super('MuseRunnerGame');
    }

    create(): void {
        const { width, height } = this.cameras.main;

        // Create character (blue rectangle) - start at ground level
        this.character = this.add.rectangle(100, height - 60, 30, 40, 0x3498db);
        this.physics.add.existing(this.character);
        const characterBody = this.character.body as Phaser.Physics.Arcade.Body;
        characterBody.setCollideWorldBounds(true);
        characterBody.setBounce(0.2); // Slight bounce for smoother landings
        characterBody.setMaxVelocity(200, 1000); // Limit horizontal speed, allow falling

        // Create platforms group
        this.platforms = this.add.group();

        // No initial platform - we'll create one when the game starts

        // Set up collisions
        this.physics.add.collider(this.character, this.platforms);

        // Musical keys setup (S-D-F-G-H-J-K)
        this.gameKeys = {
            'S': this.input.keyboard!.addKey(Phaser.Input.Keyboard.KeyCodes.S),
            'D': this.input.keyboard!.addKey(Phaser.Input.Keyboard.KeyCodes.D),
            'F': this.input.keyboard!.addKey(Phaser.Input.Keyboard.KeyCodes.F),
            'G': this.input.keyboard!.addKey(Phaser.Input.Keyboard.KeyCodes.G),
            'H': this.input.keyboard!.addKey(Phaser.Input.Keyboard.KeyCodes.H),
            'J': this.input.keyboard!.addKey(Phaser.Input.Keyboard.KeyCodes.J),
            'K': this.input.keyboard!.addKey(Phaser.Input.Keyboard.KeyCodes.K)
        };

        // Create UI
        this.scoreText = this.add.text(16, 16, 'Score: 0', {
            fontSize: '24px',
            color: '#ffffff'
        });

        this.streakText = this.add.text(16, 50, 'Streak: 0', {
            fontSize: '18px',
            color: '#f39c12'
        });

        // Instructions
        const instructionText = this.add.text(width / 2, 30, 'ðŸŽµ Press S-D-F-G-H-J-K to create platforms at different heights! ðŸŽµ', {
            fontSize: '16px',
            color: '#ecf0f1',
            fontFamily: 'Arial, sans-serif'
        }).setOrigin(0.5);

        // Key guide
        const keyGuide = this.add.text(width / 2, 60, 'S=Low â†’ D â†’ F â†’ G â†’ H â†’ J â†’ K=High', {
            fontSize: '14px',
            color: '#95a5a6',
            fontFamily: 'Arial, sans-serif'
        }).setOrigin(0.5);

        // Make instructions pulse to draw attention
        this.tweens.add({
            targets: instructionText,
            alpha: 0.6,
            duration: 1500,
            yoyo: true,
            repeat: -1
        });

        // Controls info
        this.add.text(width - 16, 16, 'v0.0.4 | ESC: Menu | R: Restart', {
            fontSize: '14px',
            color: '#95a5a6'
        }).setOrigin(1, 0);

        // ESC key to return to menu
        this.input.keyboard!.on('keydown-ESC', () => {
            this.scene.start('MainMenu');
        });

        // R key to restart
        this.input.keyboard!.on('keydown-R', () => {
            this.scene.restart();
        });

        // Start the game
        this.isGameRunning = true;
        
        // Camera follows character
        this.cameras.main.startFollow(this.character, true, 0.1, 0.1);
        this.cameras.main.setDeadzone(200, 100);

        // Reset game state
        this.score = 0;
        this.streak = 0;
        this.characterSpeed = 120;

        // Start with ground level platform (S key)
        this.startNewPlatform('S');
    }

    update(): void {
        if (!this.isGameRunning) return;

        // Auto-run character forward at a slower, more manageable speed
        const characterBody = this.character.body as Phaser.Physics.Arcade.Body;
        characterBody.setVelocityX(80); // Much slower for testing

        // Check for musical key presses - continuous platform system
        Object.keys(this.gameKeys).forEach(key => {
            if (Phaser.Input.Keyboard.JustDown(this.gameKeys[key])) {
                console.log(`Key ${key} pressed!`);
                this.switchToKey(key);
            }
        });

        // Extend current platform as character moves
        if (this.currentPlatform && this.currentKey) {
            this.extendCurrentPlatform();
        }

        // Increase difficulty over time
        this.characterSpeed = Math.min(200, 120 + (this.score / 100));

        // Game over condition
        if (this.character.y > this.cameras.main.height + 100) {
            this.gameOver();
        }

        // Clean up old platforms
        this.platforms.children.entries.forEach((platform: any) => {
            if (platform.x < this.character.x - 400) {
                platform.destroy();
            }
        });
    }

    private playNote(key: string): void {
        console.log(`Playing note ${key} at ${this.noteFrequencies[key]}Hz`);
        
        // Play musical note sound
        this.playMusicalTone(this.noteFrequencies[key]);
        
        // Visual feedback - change fill color
        this.character.setFillStyle(0xf39c12);
        this.time.delayedCall(200, () => {
            this.character.setFillStyle(0x3498db); // Back to blue
        });
    }

    private playMusicalTone(frequency: number): void {
        try {
            // Initialize audio context if needed
            if (!this.audioContext) {
                this.audioContext = new (window.AudioContext || (window as any).webkitAudioContext)();
            }

            // Create oscillator for the musical note
            const oscillator = this.audioContext.createOscillator();
            const gainNode = this.audioContext.createGain();
            
            oscillator.connect(gainNode);
            gainNode.connect(this.audioContext.destination);
            
            // Set frequency and type
            oscillator.frequency.setValueAtTime(frequency, this.audioContext.currentTime);
            oscillator.type = 'sine'; // Pure tone
            
            // Set volume envelope (attack and decay)
            gainNode.gain.setValueAtTime(0, this.audioContext.currentTime);
            gainNode.gain.linearRampToValueAtTime(0.3, this.audioContext.currentTime + 0.01);
            gainNode.gain.exponentialRampToValueAtTime(0.01, this.audioContext.currentTime + 0.5);
            
            // Play the note
            oscillator.start(this.audioContext.currentTime);
            oscillator.stop(this.audioContext.currentTime + 0.5);
            
        } catch (error) {
            console.log('Audio not available:', error);
        }
    }



    private switchToKey(key: string): void {
        // If switching to a different key, make character jump
        if (this.currentKey && this.currentKey !== key) {
            this.makeCharacterJump(key);
        }

        // End current platform and start new one
        this.endCurrentPlatform();
        this.startNewPlatform(key);
        this.playNote(key);
        
        this.score += 10;
        this.scoreText.setText(`Score: ${Math.floor(this.score)}`);
    }

    private startNewPlatform(key: string): void {
        const { height } = this.cameras.main;
        
        // Define height levels for each key
        const keyHeights: { [key: string]: number } = {
            'S': height - 80,  // Ground level
            'D': height - 120, // Level 2
            'F': height - 160, // Level 3  
            'G': height - 200, // Level 4
            'H': height - 240, // Level 5
            'J': height - 280, // Level 6
            'K': height - 320  // Highest level
        };
        
        // Color code platforms by height
        const keyColors: { [key: string]: number } = {
            'S': 0xe74c3c, // Red (lowest)
            'D': 0xf39c12, // Orange
            'F': 0xf1c40f, // Yellow
            'G': 0x2ecc71, // Green
            'H': 0x3498db, // Blue
            'J': 0x9b59b6, // Purple
            'K': 0xe91e63  // Pink (highest)
        };
        
        // Start platform slightly behind character for smooth landing
        this.platformStartX = this.character.x - 30;
        const platformY = keyHeights[key];
        
        this.currentPlatform = this.add.rectangle(this.platformStartX, platformY, 60, 15, keyColors[key]);
        this.physics.add.existing(this.currentPlatform, true);
        this.platforms.add(this.currentPlatform);
        
        this.currentKey = key;
        console.log(`Started ${key} platform at height ${platformY}`);
    }

    private extendCurrentPlatform(): void {
        if (!this.currentPlatform) return;
        
        // Extend platform to character's current position
        const newWidth = this.character.x - this.platformStartX + 50;
        const newX = this.platformStartX + newWidth / 2;
        
        this.currentPlatform.width = Math.max(20, newWidth);
        this.currentPlatform.x = newX;
        
        // Update physics body
        const body = this.currentPlatform.body as Phaser.Physics.Arcade.StaticBody;
        body.setSize(this.currentPlatform.width, this.currentPlatform.height);
        body.updateFromGameObject();
    }

    private endCurrentPlatform(): void {
        // Current platform stays in place, just stop extending it
        this.currentPlatform = null;
        this.currentKey = null;
    }

    private makeCharacterJump(newKey: string): void {
        const characterBody = this.character.body as Phaser.Physics.Arcade.Body;
        
        // Calculate height difference for smooth jumping
        const currentHeight = this.getKeyHeight(this.currentKey!);
        const newHeight = this.getKeyHeight(newKey);
        const heightDiff = Math.abs(currentHeight - newHeight);
        
        // Base jump velocity scaled by height difference
        const baseJump = 250;
        const jumpVelocity = baseJump + (heightDiff * 0.8);
        
        // Always jump (even for same level to create smooth transition)
        characterBody.setVelocityY(-jumpVelocity);
        
        console.log(`Character jumping from ${this.currentKey} to ${newKey} (velocity: ${jumpVelocity})`);
    }

    private getKeyHeight(key: string): number {
        const { height } = this.cameras.main;
        const keyHeights: { [key: string]: number } = {
            'S': height - 80,
            'D': height - 120,
            'F': height - 160,
            'G': height - 200,
            'H': height - 240,
            'J': height - 280,
            'K': height - 320
        };
        return keyHeights[key];
    }

    private updateScore(points: number): void {
        this.score += points;
        this.streak++;
        
        this.scoreText.setText(`Score: ${Math.floor(this.score)}`);
        this.streakText.setText(`Streak: ${this.streak}`);
    }

    private gameOver(): void {
        this.isGameRunning = false;
        this.streak = 0;
        
        const { width, height } = this.cameras.main;
        
        // Stop camera follow
        this.cameras.main.stopFollow();
        
        // Game over screen
        const gameOverBg = this.add.rectangle(
            this.cameras.main.scrollX + width / 2,
            this.cameras.main.scrollY + height / 2,
            width,
            height,
            0x000000,
            0.8
        );
        gameOverBg.setScrollFactor(0);
        
        const gameOverTitle = this.add.text(
            this.cameras.main.scrollX + width / 2,
            this.cameras.main.scrollY + height / 2 - 80,
            'Game Over!',
            {
                fontSize: '48px',
                color: '#e74c3c',
                fontFamily: 'Arial, sans-serif'
            }
        ).setOrigin(0.5).setScrollFactor(0);

        const finalScore = this.add.text(
            this.cameras.main.scrollX + width / 2,
            this.cameras.main.scrollY + height / 2 - 20,
            `Final Score: ${Math.floor(this.score)}`,
            {
                fontSize: '24px',
                color: '#ffffff',
                fontFamily: 'Arial, sans-serif'
            }
        ).setOrigin(0.5).setScrollFactor(0);

        const restartText = this.add.text(
            this.cameras.main.scrollX + width / 2,
            this.cameras.main.scrollY + height / 2 + 30,
            'Press R to restart or ESC for menu',
            {
                fontSize: '18px',
                color: '#bdc3c7',
                fontFamily: 'Arial, sans-serif'
            }
        ).setOrigin(0.5).setScrollFactor(0);

        // Pulsing restart text
        this.tweens.add({
            targets: restartText,
            alpha: 0.5,
            duration: 1000,
            yoyo: true,
            repeat: -1
        });

        // Send score to parent (Reddit)
        if (window.parent) {
            window.parent.postMessage({
                type: 'GAME_OVER',
                data: { finalScore: Math.floor(this.score) }
            }, '*');
        }
    }
}